import   React                from 'react'          ;
import { bindActionCreators } from 'redux'          ;
import { connect }            from 'react-redux'    ;

import   actions              from 'actions/rules'  ;
import   Popup                from 'controls/popup' ;

class Rules extends React.PureComponent {
  render() {
    let ps = {textIndent: '1.5em'};
    return (         
      <Popup role="rules" visible={this.props.rulesVisible} handler={this.props.closeRules} header=" ">
        <p style={ps}>,    -     90-.            !</p>
        <p style={ps}> -     ,  ,  ,     .</p>
        <div className="image"></div>
        <p style={ps}>       .      :  -   ,   -       .</p>
        <p style={ps}>  ?     -      ...</p>
      </Popup>
    );
  }
}

const mapStateToProps = function(state) {
  return {
    rulesVisible: state.popup.rulesVisible
  };
}

const mapDispatchToProps = function(dispatch) {
  return {
    closeRules: bindActionCreators(actions.close, dispatch)
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(Rules);


